export default {
	data() {
		return {
			city: ['选择地址', '', ''],
			bossarea: '',
			avatar: '',
			upAvatar: '',
			License: '',
			uplicense: false,
			video: '',
			upvideo: false,
			img: '',
			upimg: false,
			sex: 0,
			cid: 1,
			jobIndex: 0,
			sexIndex: 0,
			jobArr: '',
			sexArr: ['女', '男'],
			jobId: ''
		}
	},
	onLoad(option) {
		var _this = this
		// 判断用户是否注册,已注册用户类型
		let openid = uni.getStorageSync('openid')
		_this.$http.changeLogin({
			openId: openid
		}).then(res => {
			let type = res.data.data;
			console.log("type", type);
			if (type == 2) {
				console.log("hahah");
				uni.setStorageSync('userType', 2)
				uni.reLaunch({
					url: '../index/index'
				})
			} else if (type == 3) {
				uni.setStorageSync('userType', 3)
				uni.reLaunch({
					url: '../index01/index'
				})
			}
		})
		uni.loadFontFace({
			family: 'Alibaba PuHuiTi',
			source: 'url("https://www.mastervan.cn/static/project/dolphin/static/font/alifont/AlibabaPuHuiTi-Light.ttf")',
			success: function() {
				console.log('load font success')
			},
			fail() {
				//console.log(456)
			}
		})
		this.avatar = uni.getStorageSync('headpic')
		_this.$http.getWork({
			openId: openid
		}).then(res => {
			let arr1 = [];
			let arr2 = [];
			let dataArr = res.data.data;
			for (let i in dataArr) {
				arr1.push(dataArr[i].cname);
				arr2.push(dataArr[i].cid)
			}
			_this.jobArr = arr1;
			_this.jobId = arr2;
		})
	},
	methods: {
		// 性别选择
		bindSexChange: function(e) {
			//console.log('picker发送选择改变，携带值为', e.target.value)
			this.sexIndex = e.target.value
		},
		// 职业选择
		bindJobChange: function(e) {
			//console.log('picker发送选择改变，携带值为', e.target.value)
			this.jobIndex = e.target.value
		},
		upBoss: function() {
			uni.navigateTo({
				url: '/pages/status/index'
			})
		},
		bossArea: function(e) {
			this.city = '';
			this.city = e.detail.value
			// console.log(typeof this.city)
		},
		changHeadpic: function() {
			uni.navigateTo({
				url: '/pages/upHead/index?status=' + 0
			})
		},
		upLicense: function() {
			var that = this;
			wx.chooseImage({
				count: 1,
				sizeType: ['compressed'],
				sourceType: ['album', 'camera'],
				success: function(res) {
					console.log(res)
					var avatar = res.tempFilePaths;
					that.License = avatar;
					that.uplicense = true;
					that.$store.commit('setQiangSt', true)
					let upLv = that.$store.getters.isSt;
					console.log(upLv)
				},
				fail: function() {
					// fail
				},
				complete: function() {
					// complete
				}
			})
		},
		upVideo: function() {
			var _this = this;
			uni.chooseVideo({
				sourceType: ['album', 'camera'],
				maxDuration: 10,
				success: function(res) {
					// self.src = res.tempFilePath;
					let v = res.tempFilePath
					_this.video = v
					_this.upvideo = true;
					console.log(res)
				},
				fail: function(res) {
					console.log(res)
				},
			});
		},
		upImg: function() {
			var that = this;
			wx.chooseImage({
				count: 3, // 最多可以选择的图片张数，默认9
				sizeType: ['compressed'], // original 原图，compressed 压缩图，默认二者都有
				sourceType: ['album', 'camera'], // album 从相册选图，camera 使用相机，默认二者都有
				success: function(res) {
					//console.log(res.tempFilePaths)
					var avatar = res.tempFilePaths;
					that.img = avatar;
					that.upimg = true;
					//console.log(that.img)
				},
				fail: function() {
					// fail
				},
				complete: function() {
					// complete
				}
			})
		},
		submitData: function(e) {
			let _this = this;
			let openid = uni.getStorageSync('openid');
			e.detail.value.address = this.city[0] + this.city[1] + this.city[2];
			e.detail.value.openId = openid;
			e.detail.value.roleType = 3;
			e.detail.value.headUrl = this.avatar;
			e.detail.value.sex = this.sexIndex;
			e.detail.value.cid = this.jobId[this.jobIndex];
			e.detail.value.age = e.detail.value.age;
			e.detail.value.height = e.detail.value.height;
			e.detail.value.weight = e.detail.value.weight;
			e.detail.value.bust = e.detail.value.bust;
			e.detail.value.hipLine = e.detail.value.hipLine;
			if(!_this.util.phoneNumber(e.detail.value.phone)) {
				return
			}
			if (e.detail.value.address == '选择地址' || e.detail.value.userName == '' || e.detail.value.age == '' || e.detail.value
				.phone == '' || e.detail.value.height == '' || e.detail.value.weight == '' || e.detail.value.bust == '' || e.detail
				.value.hipLine == '' || this.uplicense == false || this.upvideo == false || this.upimg == false) {
				uni.showModal({
					title: '提示',
					content: '请填写完整信息'
				})
				return false
			}
			uni.showLoading();
			uni.request({
				url: _this.$store.state.baseurl + '/user/insertUser',
				data: e.detail.value,
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {
					if (res.data.data == 1) {
						console.log(_this.video)
						uni.uploadFile({
							url: _this.$store.state.baseurl + '/user/videoUpload',
							filePath: _this.video,
							name: 'videoUrl',
							formData: {
								openId: e.detail.value.openId
							},
							method: 'POST',
							header: {
								"Content-Type": "multipart/form-data"
							},
							success: (res) => {
								console.log(res)
							}
						});
						//头像
						uni.uploadFile({
							url: _this.$store.state.baseurl + '/user/fileUpload',
							filePath: _this.avatar,
							name: 'headUrl',
							formData: {
								openId: openid
							},
							method: 'POST',
							header: {
								"Content-Type": "multipart/form-data"
							},
							success: (res) => {}
						});
						//模卡
						uni.uploadFile({
							url: _this.$store.state.baseurl + '/user/fileUpload',
							filePath: _this.License[0],
							name: 'card',
							formData: {
								openId: e.detail.value.openId
							},
							method: 'POST',
							header: {
								"Content-Type": "multipart/form-data"
							},
							success: (res) => {

							}
						});

						//照片
						for (let i = 0; i < _this.img.length; i++) {
							// console.log(_this.img[2])
							uni.uploadFile({
								url: _this.$store.state.baseurl + '/user/fileUpload',
								filePath: _this.img[i],
								name: 'selfPhoto',
								formData: {
									openId: e.detail.value.openId
								},
								method: 'POST',
								header: {
									"Content-Type": "multipart/form-data"
								},
								success: (res) => {
									//openId、或SessionKdy存储//隐藏loading
									uni.hideLoading();
									uni.reLaunch({ //信息更新成功后跳转
										url: '/pages/status/index'
									});
								}
							});
						}
					} else {
						//console.log('上传失败')
						uni.hideLoading();
					}

				}
			})
		}
	}
}
